@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
.App {
  font-family: sans-serif;
  text-align: center;
}

.App {
  min-height: 100vh;
  background-image: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);
}
.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 5px 10px #979b91;
}
.search-button {
  background: #4da1ab;
  border: 5px solid white;
  border-radius: 8px;
  padding: 10px 20px;
  color: white;
  font-size: larger;
  margin: 0 0 0 10px;
}
.search-button:hover {
  background-color: #fa709a;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.recipe_recipe__1BpIk {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(63, 60, 60);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-image: linear-gradient(
    to right,
    #e4afcb 0%,
    #b8cbb8 0%,
    #b8cbb8 0%,
    #e2c58b 30%,
    #c2ce9c 64%,
    #7edbdc 100%
  );
  align-items: center;
  min-width: 40%;
  font-family: "Lobster", cursive;
}

.recipe_image__3f_Yv {
  border-radius: 10px;
  margin: 0px 0px 20px 0px;
  width: 300px;
  height: 200px;
}

.recipe_block__6bspi {
  background-image: linear-gradient(
    to right,
    #e4afcb 0%,
    #b8cbb8 0%,
    #b8cbb8 0%,
    #e2c58b 30%,
    #c2ce9c 64%,
    #7edbdc 100%
  );
  display: -ms-grid;
  border-radius: 10px;
  margin: 0px 0px 20px 0px;
  width: 300px;
  height: 200px;
}

.recipe_overlay__3-ohQ {
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: 200px;
  background: rgba(0, 0, 0, 0.6);
  color: #b8cbb8;
  display: flex;
  align-items: center;
  justify-content: center;
}

