@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");
.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(63, 60, 60);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-image: linear-gradient(
    to right,
    #e4afcb 0%,
    #b8cbb8 0%,
    #b8cbb8 0%,
    #e2c58b 30%,
    #c2ce9c 64%,
    #7edbdc 100%
  );
  align-items: center;
  min-width: 40%;
  font-family: "Lobster", cursive;
}

.image {
  border-radius: 10px;
  margin: 0px 0px 20px 0px;
  width: 300px;
  height: 200px;
}

.block {
  background-image: linear-gradient(
    to right,
    #e4afcb 0%,
    #b8cbb8 0%,
    #b8cbb8 0%,
    #e2c58b 30%,
    #c2ce9c 64%,
    #7edbdc 100%
  );
  display: -ms-grid;
  border-radius: 10px;
  margin: 0px 0px 20px 0px;
  width: 300px;
  height: 200px;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: 200px;
  background: rgba(0, 0, 0, 0.6);
  color: #b8cbb8;
  display: flex;
  align-items: center;
  justify-content: center;
}
